import * as React from 'react';
import { ProcessAndArena, ProcessImplName, ViewName, ProcessState, GenericMessageHandler, Payload, Message, message, addHandlerClass, addRenderer, addUi } from './Base';
import { SimpleSend } from '../components/SendButton';
import { MessageLog } from '../components/MessageLog';
import { Popup } from '../components/Popup';
import { Modal, Button } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import ClientView from './../components/Client';

const TokenNode : ProcessImplName = "TokenNode";

const WithToken : ViewName = "with";
const WithoutToken : ViewName = "without";

interface NodeState {
  token?: string; // token I have or undefined
  next: string; // next station.
}

interface NodePS extends ProcessState<NodeState> {
  type: typeof TokenNode;
  view: typeof WithToken | typeof WithoutToken;
  title: string;
}

export function createTokenNode(title: string, name: string, next: string, token?: string): NodePS {
  return {
    title: title,
    name: name,
    type: TokenNode,
    view: token === undefined ? WithoutToken : WithToken,
    mem: {
      token: token,
      next: next
    },
    queue: [],
  };
}

class InitPassHandler extends GenericMessageHandler<NodeState, NodePS, Payload, InitPassHandler> {
  name = "TokenNode/InitPass";
  handle(node: NodePS, action: Message<Payload>) {
    if (node.view !== WithToken) {
      console.error(`Invalid message, order to pass while I have no token? ${JSON.stringify(node)}`);
      return node;
    }

    
    return {
      ...node,
      queue: node.queue.concat(message(action.arena, node, [node.mem.next], tokenMessage({token: node.mem.token})())),
      mem: { ...node.mem, token: undefined },
      view: WithoutToken,
    };
  }
}

const initPassMessage = addHandlerClass(TokenNode, InitPassHandler);


// selle snumiga saadetakse reaalselt tokeneid.
interface TokenMessage extends Payload {
  token: string;
}

class PostTokenHandler extends GenericMessageHandler<NodeState, NodePS, TokenMessage, PostTokenHandler> {
  name = "TokenNode/PostToken";
  // sissetulev token
  handle(node: NodePS, action: Message<TokenMessage>) {
    if (node.view === WithToken) {
      console.log(`${action.sender} sent token '${action.message.token}' to ${node.name}. We already have a token (${node.mem.token}). Will send the token back`);
      // send it back.
      return {
        ...node,
        queue: node.queue.concat(message(action.arena, node, [action.sender], action.message)),
      };
    } else {
      // tokenit polnud, jtame meelde.
      return {
        ...node,
        mem: { ...node.mem, token: action.message.token },
        view: WithToken,
      };

    }
  }
}

const tokenMessage = addHandlerClass<TokenMessage, PostTokenHandler>(TokenNode, PostTokenHandler);


interface TargetPayload extends Payload {
  target: string;
}

class UpdateTargetHandler extends GenericMessageHandler<NodeState, NodePS, Payload, InitPassHandler> {
  name = "TokenNode/UpdateTarget";
  updateMem(mem: NodeState, action: Message<TargetPayload>) {
    return { ...mem, next: action.message.target };
  }
}

const updateNextMessage = addHandlerClass(TokenNode, UpdateTargetHandler);


