import { ProcessImplName, ViewName, ProcessState, Payload, GenericMessageHandler, Message, addHandlerClass, ProcessAndArena, addRenderer } from './Base';
import * as React from 'react';
import { SimpleSend } from '../components/SendButton';
import { MessageLog } from '../components/MessageLog';

const Counter : ProcessImplName = "Counter";

const CounterDefaultView : ViewName = "_";

// "rakenduse olek"
export interface CounterState extends ProcessState<number> {
	type: typeof Counter;
	view: typeof CounterDefaultView;
}

// "bootloader", tekitab rakenduse algoleku
export function createCounter(name: string) {
	return {
		name: name,
		type: Counter,
		view: CounterDefaultView,
		mem: 0,
	} as CounterState;
}

class IncrementMessageHandler extends GenericMessageHandler<number, CounterState, Payload, IncrementMessageHandler> {
	name = "Counter/INC";
	updateMem(mem: number, action: Message<Payload>) { return mem + 1; }
}


class DecrementMessageHandler extends GenericMessageHandler<number, CounterState, Payload, DecrementMessageHandler> {
	name = "Counter/DEC";
	updateMem(mem: number, action: Message<Payload>) { return mem - 1; }
}

const incrementMessage = addHandlerClass(Counter, IncrementMessageHandler);
const decrementMessage = addHandlerClass(Counter, DecrementMessageHandler);


interface DefaultViewProps extends ProcessAndArena<number> {
	process: CounterState;
	arena: string;
}

const counterRenderer : React.FC<DefaultViewProps> = ({process, arena}) => {
	return (
	<div>
		<span className="process-state">{ process.mem }</span>
		<span className="process-state">
			<SimpleSend arena={arena} owner={process} text="+" message={incrementMessage()}/>
			<SimpleSend arena={arena} owner={process} text="-" message={decrementMessage()}/>
		</span>
		<span className="process-state">
			<MessageLog arena={arena} owner={process} showSelf={true} maxRows={4}/>
		</span>
	</div>
	);
};


addRenderer(Counter, CounterDefaultView, counterRenderer);
