import * as React from 'react';

interface PopupProps {
    text: any;
    open?: boolean; // initial state
}

export class Popup extends React.Component<PopupProps, { open: boolean }> {
    state = { open: false };

    render() {
        return (
            <div>
                <div className={ this.state.open ? "popup-visible" : "popup-hidden" }>
                    <div className="popup-content">
                        <div className="popup-close" onClick={() => this.setState({open: false})}></div>
                        {this.props.children}
                    </div>
                </div>
                <button className="btn btn-primary" onClick={() => this.setState({open: true})}>{this.props.text}</button>
            </div>
        );
    }
}