import React from 'react';
import { BackendStateUpdater } from '../../../postMessage';
import { ConsentServiceState, PurposeDeclaration } from '../types';
import { ConsentServiceBackend, Backend, CSBackend } from './backendAPI';
import VeeraLayout from './../../../components/veera/layout';
import Tabs from './../../../components/tabs/Tabs';
import { MyConsents } from './MyConsents';
import { ConsentHistory } from './ConsentHistory';
import { business } from '../../../i18n';
import { renderText } from '../../../util';
import { Modal, Button } from "react-bootstrap";


function LoginScreen() {
  return (
    <div className="section">
      <h4>Nusoleku andmiseks sisene Tervise- ja Heaolu Infossteemide Keskuse nusolekute andmise keskkonda:</h4>
      <p className="mt-3"><a href="/ui/ria" className="btn btn-primary">Sisene</a></p>
    </div>
  );
}

interface APDProps {
  backend: ConsentServiceBackend;
  activeDecl?: PurposeDeclaration;
  tabRef: React.RefObject<Tabs>;
  label: string;
}

interface State {
  signPopup: boolean;
  returnPopup: boolean;
  savedClientId?: string;  
}

class ActivePurposeDeclaration extends React.PureComponent<APDProps, State> {
  state : State = {
    signPopup: false,
    returnPopup: false,
  };

  constructor(props: APDProps) {
    super(props);
    this.showConsentTab = this.showConsentTab.bind(this);
  }

  showSignPopup(show: boolean) {
    this.setState(s => ({ ...s, signPopup: show }));
  }

  showReturnPopup(show = true, clientId?: string) {
    this.setState(s => ({ ...s, returnPopup: show, savedClientId: clientId }))
  }

  toggleSignPopup() {
    this.setState(s => ({ ...s, signPopup: !s.signPopup }))
  }

  showConsentTab(ev: any) {
    ev.preventDefault();
    if (this.props.tabRef.current) this.props.tabRef.current.onClickTabItem("Minu nusolekud");
  };

  Popover() {
    return (
      <>
        <Modal show={this.state.signPopup} onHide={() => this.toggleSignPopup()}>
          <Modal.Body>
            <h4>Nusoleku andmiseks allkirjasta nusolek digitaalselt</h4>
          </Modal.Body>
          <Modal.Footer>
            <button className="btn btn-primary" onClick={(ev) => {
              this.props.backend.giveConsentByPurpose(
                ev,
                this.props.activeDecl!.services,
                this.props.activeDecl!.clientId,
                this.props.activeDecl!.purposeDeclarationId
              );
              this.toggleSignPopup();
              this.showReturnPopup(true, this.props.activeDecl!.clientId);
            }}>Allkirjasta</button>
            <Button variant="secondary" onClick={(ev:any) => this.toggleSignPopup()}>
              Tagasi
            </Button>
          </Modal.Footer>
        </Modal>
        <Button variant="primary" className="mr-2" onClick={(ev:any) => this.toggleSignPopup()}>
          Anna nusolek
        </Button>
        <Button variant="secondary" onClick={(ev:any) => window.close()}>
          Ei anna nusolekut
        </Button>
      </>
    );
  }

  ReturnScreen() {
    return (
    <div>
      <h4>Nusolek { business(this.state.savedClientId!, "name") }-le on antud!</h4>
      <p>Kiki oma antud nusolekuid ned ja saad hallata siit <a href="/ui/csui" onClick={this.showConsentTab}>Minu nusolekud</a></p>
      <button className="btn btn-primary" onClick={() => {
          window.close();
          this.showReturnPopup(false);
        }}>Tagasi teenusesse</button>
    </div>
    );
  }

  render() {

    const activeDecl = this.props.activeDecl;

    if (activeDecl === undefined) {
      if (this.state.returnPopup) {
        return this.ReturnScreen();
      } else {
        return (<div>Avatud taotlust pole.</div>);
      }
    }

    return (
        <div className="tab-info">
          <h4 className="mb-4 title">{ business(activeDecl.clientId, "name") } ({ business(activeDecl.clientId, "ntr") }) ksib nusolekute jrgmistel tingimustel:</h4>
            <div className="row">
              <div className="col-md">
                <div className="d-flex align-items-baseline">
                  <div><h4>Kirjeldus</h4></div>
                  <div>
                    { renderText(activeDecl.description) }
                  </div>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-md">
                <div className="d-flex align-items-end">
                  <div><h4>Kehtivus</h4></div>
                  <div>{activeDecl.validUntil && `Kehtib kuni: ${ formatTimestamp(activeDecl.validUntil)}`}</div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-md">
                <div className="d-flex align-items-end">
                  <div><h4>Nusoleku alusel saab { business(activeDecl.clientId, "name") } Sinu kohta jrgmised andmed:</h4></div>
                  <div>
                    {
                      activeDecl.services.map(sdref => {
                        const sd = this.props.backend.getServiceDeclaration(sdref);
                        if (sd === undefined) {
                          return <></>;
                        }
                        return (
                          <>
                            <h4>{sd.name}</h4>
                            { renderText(sd.description) }
                          </>
                        )
                      })
                    }
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-md mt-4 mb-2 text-muted">
                Antud nusoleku saad igal ajal thistada oma nusolekute vaatest <a href="/ui/csui" onClick={this.showConsentTab}>Minu nusolekud</a>
              </div>
            </div>
            <p className="mt-4">
              { this.Popover() }
            </p>
          </div>
    );
  }
};

const MainView = ({backend, tabRef}: any) => {

  let backendUse = backend.use();

  if (backendUse === null || backendUse === undefined) {
    return (
      <>
      <div className="section">Wait, or reinit the client UI from the arena window... '{window.name}'</div>
      </>
    );
  }

  if (backendUse.mem!.ui.user === undefined) {
    return LoginScreen();
  }

  const activeDecl = backendUse.getActivePurposeDeclaration();

  return (
    <Tabs ref={tabRef}>
      <div label="Avatud taotlused">
        <ActivePurposeDeclaration label="Avatud taotlused" backend={backendUse} activeDecl={activeDecl} tabRef={tabRef}/>
      </div>
      { MyConsents(backendUse) }
      { ConsentHistory(backendUse) }
    </Tabs>
  )
}



export class CSEnduserUI extends React.PureComponent<{}, ConsentServiceBackend> {
  backendLink: BackendStateUpdater<ConsentServiceState, ConsentServiceBackend>;
  backend: Backend;
  ref: React.RefObject<any>;

  constructor(props: any, context?: any) {
    super(props, context);
    this.backendLink = new BackendStateUpdater(csb => { this.setState(csb as ConsentServiceBackend); }, ConsentServiceBackend);
    this.backend = new Backend();
    this.ref = React.createRef();
  }

  componentDidMount() {
    if (process.env.NODE_ENV === "development") {
      require('../../../static/css/mta_visuaal.css');
    }
    document.body.classList.add('temp');
    this.backendLink.resetState();
  }

  render() {
    return (
      <CSBackend.Provider value={this.state}>
        <VeeraLayout backend={this.backend}>
          <MainView backend={this.backend} tabRef={this.ref} />
        </VeeraLayout>
      </CSBackend.Provider>
    );
  }
}

export function formatTimestamp(timestamp : number, withTime? : boolean) {
  if (withTime) {
    return Intl.DateTimeFormat(
      'et',
      {
        year : 'numeric',
        month : 'numeric',
        day : 'numeric',
        hour : 'numeric',
        minute : 'numeric',
        second : 'numeric'
      }
    ).format(new Date(timestamp*1000));
  }
  return Intl.DateTimeFormat('et').format(new Date(timestamp*1000));
}
