import { ConsentServiceBackend } from './backendAPI';
import React from 'react';
import { Consent } from '../types';
import { formatTimestamp } from './CSEnduserUI';
import { ConsentModal } from './ConsentModal';
import { business } from '../../../i18n';

export function isConsentActive(consent : Consent) {
  return !consent.revoked && consent.validUntil > Date.now()/1000;
}

function consentDescription(c : Consent) {
  return (
    `${business(c.clientId, "name")}; ${business(c.clientId, "appname")}; Nusolek antud ${formatTimestamp(c.validFrom)}` +
    `  ${ c.revoked ? "nusolek thistatud" : "kehtib kuni" } ${formatTimestamp(c.validUntil)}`
  );
}

export const MyConsents = (backend :  ConsentServiceBackend) => {

  const Table = (consents : Consent[], header : JSX.Element) => {
    return (
      <>
        <h4>{ header }</h4>
        <table className="table align-middle">
          <tbody>
            { consents.map( c => (
              <tr key={c.clientId}>
                <td>{ consentDescription(c) }</td>
                <td><ConsentModal backend={backend} consent={c} buttonText="Vaata lhemalt"/></td>
              </tr>
            ))}
          </tbody>
        </table>
      </>
    )
  }

  const Body = (backend : ConsentServiceBackend) => {

    if (backend.getConsents().length === 0) {
      return (
        <p>Nusolekuid ei leitud.</p>
      )
    }

    const header = (title: string, length: number) => (
      <h4>
        <span>{title}</span>
        {length === 0 &&
          <span className="badge badge-default ml-2">
            <span className="badge-text">Puuduvad</span>
          </span>
        }
      </h4>
    )

    return (
      <>
        { Table( backend.getConsents(true),  header("Kehtivad nusolekud:", backend.getConsents(true).length)) }
        { Table( backend.getConsents(false), header("Kehtetud nusolekud:", backend.getConsents(false).length)) }
      </>
    )
  }

  return (
    <div label = "Minu nusolekud"> { Body(backend) } </div>
  )
}
