import { ConsentServiceBackend } from "./backendAPI";
import { Consent } from "../types";
import React from "react";
import { Modal, Button } from "react-bootstrap";
import { formatTimestamp } from "./CSEnduserUI";
import { isConsentActive } from "./MyConsents";
import { business } from "../../../i18n";
import { renderText } from "../../../util";

export const ConsentModal: React.FC<{backend: ConsentServiceBackend, consent: Consent, buttonText : string}> = ({backend, consent, buttonText}) => {

    const [show, setShow] = React.useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const handleRevoke = (ev:any) => {
      setShow(false);
      backend.revokeConsent(ev, consent.consentReference);
    }

    if (backend.mem === undefined) {
        return null;
      }
      const dec = backend.mem.db.purposeDeclarations.find(p => p.purposeDeclarationId === consent.purposeDeclarationId);
      if (dec === undefined) {
        return null;
      }

      const rowView = (name: string, value: any, last?: boolean) => {
        return (
          <div className="row" key={name}>
            <div className="col-md">
              <div className= { last ? "d-flex align-items-end" : "d-flex align-items-baseline" }>
                <div><h4> { name }</h4></div>
                <div>{ value }</div>
              </div>
              </div>
          </div>
        )
      }

    const Body = () => {
      let services = dec.services.map(sdref => {
        const sd = backend.getServiceDeclaration(sdref);
        if (sd === undefined) {
          return <></>;
        }
        return (
          <>
            <h4>{sd.name}</h4>
            { renderText(sd.description) }
          </>
        )
      })
      return (
        <div className="tab-info">
          { rowView('Nusoleku saaja:', `${business(dec.clientId, "name")} (${business(dec.clientId, "ntr")})`) }
          { rowView('Rakendus:', `${ business(dec.clientId, "appname") }` ) }
          { rowView('Nusolek antud:', formatTimestamp(consent.validFrom)) }
          { isConsentActive(consent)
              ? rowView('Nusolek kehtib kuni:', formatTimestamp(consent.validUntil))
              : rowView('Nusolek thistatud:', formatTimestamp(consent.validUntil)) }
          { rowView('Eesmrk:', renderText(dec.description), true) }
          { rowView(`Nusoleku alusel saab ${business(dec.clientId, "name")} Sinu kohta jrgmised andmed:`, services, true)}
        </div>
      )
    }

    return (
    <>
      <Modal show={show} onHide={handleClose} size="lg">
        <Modal.Header>
          <Modal.Title>Detailid</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Body />
        </Modal.Body>
        <Modal.Footer>
          { isConsentActive(consent) &&
          <Button variant="primary" onClick={(ev:any) => handleRevoke(ev)}>
            Vtan nusoleku tagasi
          </Button>
          }
          <Button variant="secondary" onClick={handleClose}>
            Sule
          </Button>
        </Modal.Footer>
      </Modal>
      <Button variant="primary" onClick={handleShow}>
        { buttonText }
      </Button>
    </>
    )
};