import React, { Suspense } from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Theatre } from './components/Theatre';
import { useTranslation } from 'react-i18next';
import { CSEnduserUI } from './process/consentService/altUI/CSEnduserUI';
import { Ria } from './process/consentService/altUI/ria';

const Page = () => {
  const { i18n } = useTranslation();

  const changeLanguage = (lng: string) => {
    i18n.changeLanguage(lng);
  };

  return (
    <div className="App">
        <Router>
          <Switch>
            <Route path="/ui/ria">
              <Ria/>
            </Route>
            <Route path="/ui/csui">
              <CSEnduserUI/>
            </Route>
            <Route path="/">
              <nav className="navbar navbar-light bg-light mb-4">
                <span className="navbar-brand">
                  <img alt="logo" src={require('./logo.svg')} width="40" />
                </span>
                <div className="language">
                  <a href="#!"
                    onClick={(e) => {e.preventDefault(); changeLanguage('et')}}
                    className={ i18n.language === 'et' ? 'active': '' }>et</a>
                  <a href="#!"
                    onClick={(e) => {e.preventDefault(); changeLanguage('en')}}
                    className={ i18n.language === 'en' || i18n.language === 'en-US' ? 'active': '' }>en</a>
                </div>
              </nav>
              <div className="container-fluid">
                <Theatre/>
              </div>
            </Route>
          </Switch>
        </Router>
    </div>
  );
}

// loading component for suspense fallback
const Loader = () => (
  <div className="App">
    <div>loading...</div>
  </div>
);

const App: React.FC = () => {
  return (
    <Suspense fallback={<Loader />}>
      <Page />
    </Suspense>
  );
}

export default App;
