import { ConsentServiceBackend } from './backendAPI';
import { UsageRecord } from '../types';
import React from 'react';
import { formatTimestamp } from './CSEnduserUI';
import { ConsentModal } from './ConsentModal';
import { business } from '../../../i18n';

function usageRecordDescription(u: UsageRecord) {
  if (u.result === 'OK') {
    return formatTimestamp(u.usageTime, true) + ' ' + business(u.clientId, "name") + ' sai Sinu andmed nusoleku alusel';
  } else if (u.consentReference) {
    return formatTimestamp(u.usageTime, true) + ' ' + business(u.clientId, "name") + ' ksis, kuid ei saanud Sinu andmeid thistatud nusoleku alusel';
  } else {
    return formatTimestamp(u.usageTime, true) + ' ' + business(u.clientId, "name") + ' ksis, kuid ei saanud Sinu andmeid nusoleku puudumise tttu';
  }
} 

export const ConsentHistory = (backend : ConsentServiceBackend) => {

  const Modal = (backend : ConsentServiceBackend, consentReference : string) => {
    if (!backend.mem) {
      return;
    }
    const con = backend.mem.db.consents.find(c => c.consentReference === consentReference);
    if (!con) {
      return;
    }
    return (<td><ConsentModal backend={backend} consent={con} buttonText="Vaata nusolekut"/></td>)
  }

  const Body = (backend : ConsentServiceBackend) => {
    if (backend.getUsageLogRecords().length === 0) {
      return (
        <p>Nusolekute kasutamise sndmusi ei leitud.</p>
      )
    }
    return (
      <>
        <h4>Ajalugu:</h4>
        <table className="table align-middle">
          <tbody>
          {
            backend.getUsageLogRecords().map(
              u => (
                <tr key={u.clientId}>
                  <td>{ usageRecordDescription(u) }</td>
                  { u.consentReference ? Modal(backend, u.consentReference) : (<></>) }
                </tr>
              )
            )
          }
          </tbody>
        </table>
      </>
    )
  } 

  return (
    <div label="Nusolekute kasutamise ajalugu">
      { Body(backend) }
    </div>
  );
};
