import * as React from "react";

interface Props {}

interface State {
  view: string;
  loading: boolean;
}

const LOGIN = "login";
const SELECT_SERVICE = "selectService";
const SERVICE_V1 = "serviceV1";

const Header = () => (
  <div className="app-header">
    <div className="app-user-icon">
      <img alt="user icon" src={require('./../static/assets/images/user-icon.svg')} />
    </div>
    <p className="app-user">TERE MARI-LIIS MNNIK</p>
  </div>
);

class ClientView extends React.PureComponent<Props, State> {

  state = {
    view: LOGIN,
    loading: false
  };

  changeView = (view: string) => {
    this.setState({ loading: true });
    setTimeout(() => {
      this.setState({loading: false});
      this.setState({view: view});
    }, 1000);
  };

  render() {
    switch (this.state.loading) {
      case true:
      return (
        <div className="d-flex align-items-center">
          <strong>Loading...</strong>
          <div className="spinner-border ml-auto" role="status" aria-hidden="true"></div>
        </div>
      );
      default:
        switch (this.state.view) {
          case LOGIN:
          return (
            <div>
              <h4>Tere! Teenuse kasutamiseks logi palun sisse.</h4>
              <button className="btn btn-light border" onClick={() => this.changeView(SELECT_SERVICE)}>Logi sisse kasutajana Mari-Liis Mnnik 47302200234</button>
              <p className="mt-4">
                <img alt="illustration" src={require('./../static/assets/images/login.svg')} width="280" />
              </p>
            </div>
          );
          case SELECT_SERVICE:
          return (
            <div>
              <Header />
              <h4>Vali teenus mida soovid kasutada:</h4>
              <button className="btn btn-dark" onClick={() => this.changeView(SERVICE_V1)}>Vaktsineerimiste meeldetuletused</button>
            </div>
          );
          case SERVICE_V1:
          return (
            <div>
              <Header />
              <h4>Teenuse tekst TODO</h4>
              <p className="text-left">
                <small>Enne nusoleku andmist veendu, et oled tutvunud meie privaatsuspoliitika ja kasutustingimustega, mis on leiavad <a href="JavaScript:void(0);">siin</a>. Nusoleku andmiseks suuname Sind e-Tervise nusolekute keskkonda, mida haldab Tervise- ja Heaolu Infossteemide Keskus.</small>
              </p>
              <button className="btn btn-lg btn-primary">Annan nusoleku</button>
            </div>
          );
          default:
          return(null)
        }
    }
  }
}

export default ClientView;