import React from 'react';
import { connect } from 'react-redux';

import { ArenaState, Arena, reduceArena, ArenaActionType } from './Arena';
import { AppState } from '../store';
import { ByNameStore, reduceByName } from '../byNameStore';
import { initialTheatre } from '../config';
import { AnyAction } from 'redux';

type Arenas = ByNameStore<ArenaState>;

export interface TheatreState {
    name: string;
    arenas: Arenas;
    active: string;
}

export function selectArena(state: AppState, arena: string) {
    return state.theatre.arenas.byName[arena];
}

const SELECT_ARENA = "Theatre/SELECT_ARENA";
interface SelectArenaAction extends AnyAction {
    type: typeof SELECT_ARENA;
    arena: string;
}

// function selectArenaMessage(arena: string): SelectArenaAction {
//     return { type: SELECT_ARENA, arena: arena };
// }

export function reduceTheatre(state: TheatreState = initialTheatre(), action: any): TheatreState {
    switch (action.type) {
        case SELECT_ARENA:
            return ({ ...state, active: action.arena });
        default:
            return ({ ...state, arenas: reduceByName(state.arenas, (a) => reduceArena(a, action as ArenaActionType)) });
        }
}

interface TheatreProps {
    theatre: TheatreState;
}

class _Theatre extends React.PureComponent<TheatreProps> {
    // <ArenaSelector arenaNames={ this.props.theatre.arenas.allNames }/>
    componentDidMount() {
        require('../App.css');
        require('../bootstrap.css');
    }

    render() {
        return (
            <div>
                <Arena arena={ this.props.theatre.arenas.byName[this.props.theatre.active] }/>
            </div>

        )
    }
}

export const Theatre = connect(mapStateToProps)(_Theatre);

function mapStateToProps(state: AppState) {
    return { theatre: state.theatre }
}


